
.App {
  margin: 0px;
  text-align: center;
  color: black;
}

.App h1 {
  margin: 0px;
  padding: 10px;
  font-size: 3em;
}

h4 {
  font-size: 2em;
  padding: 0px;
  margin: 0px;
}

.restaurant {
  display: -ms-flexbox;
  display: flex;
}

.btn {
  width: 70px;
  padding: 10px;
  margin: 10px;
  text-align: center;
  border: 2px solid red;
  color: red;
  background-color: white;
}

.menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-top: 20px;
  width: 70%;
  height: 1000px;
  margin-left: 50px;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

img {
  width: 200px;
}


.MenuCard {
  display: -ms-flexbox;
  display: flex;
  margin: 10px;
  padding: 10px;
  width: 350px;
  height: 250px;
  border: 1px solid white;
  border-radius: 5px;
}

.MenuCardDetails {
  width: 50%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;  
}

.OrderCart {
  margin-top: 20px;
  margin-left: 50px;
  width: 20%;
  height: 800px;
  border: 1px solid #eee;
  -webkit-box-shadow: 0 2px 3px #ccc;
          box-shadow: 0 2px 3px #ccc;

}

.orderChosen {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.orderChosen span{
  width: 30%;
  text-align: center;
  padding: 5px;
}

.orderTotal {
  padding: 10px;
  margin-left: 20px;
  text-align: left;
  width: 100%;
}

.orderTotal span {
  padding: 10px;
  text-align: right;
  margin-right: 30%;
}



select {
  width: 200px;
  height: 30px;
  border-radius: 5px;
  padding: 3px;
}


